import { useForm } from "react-hook-form";

export interface CustomerDetails {
    name: string;
    phone: string;
    note: string;
    email: string;
    mailingList: boolean;
}

export default function CustomerForm(props: { callback: (customerDetails: CustomerDetails) => void, initialCustomerDetails?: CustomerDetails }): JSX.Element {
    // eslint-disable-next-line @typescript-eslint/unbound-method
    const { register, handleSubmit, errors } = useForm<CustomerDetails>({
        defaultValues: {
            mailingList: props.initialCustomerDetails ? props.initialCustomerDetails.mailingList : true,
            name: props.initialCustomerDetails ? props.initialCustomerDetails.name : "",
            phone: props.initialCustomerDetails ? props.initialCustomerDetails.phone : "",
            note: props.initialCustomerDetails ? props.initialCustomerDetails.note : "",
            email: props.initialCustomerDetails ? props.initialCustomerDetails.email : "",
        }
    });
    const onSubmit = handleSubmit((customerDetails) => {
        props.callback(customerDetails);
    });

    return (
        <form onSubmit={onSubmit}>
            <h3 className="mt-2" style={{ fontFamily: "Permanent Marker" }}>Who:</h3>
            <label htmlFor="name" className="form-label mt-2">Name:</label>
            <input className={`form-control ${errors.name ? "is-invalid" : ''}`} id="name" name="name" ref={register({ required: 'Name required', maxLength: { value: 50, message: 'Too long!' } })} />
            { errors.name && <div className="invalid-feedback">{errors.name.message}</div>}
            <label htmlFor="phone" className="form-label mt-2">Phone Number:</label>
            <input className={`form-control ${errors.phone ? "is-invalid" : ''}`} id="phone" name="phone" ref={register({ required: 'Phone number required', maxLength: { value: 12, message: 'Too long' }, minLength: { value: 7, message: 'Too short' } })} />
            { errors.phone && <div className="invalid-feedback">{errors.phone.message}</div>}
            <label htmlFor="note" className="form-label mt-2">Note to us (optional):</label>
            <textarea className={`form-control ${errors.note ? "is-invalid" : ''}`} id="note" name="note" ref={register({ maxLength: { value: 200, message: 'Too long!' } })} placeholder='e.g. "Write `Love from Mike` on the box", "Ring the buzzer"' />
            { errors.note && <div className="invalid-feedback">{errors.note.message}</div>}
            <label htmlFor="email" className="form-label mt-2">Email:</label>
            <input className={`form-control ${errors.email ? "is-invalid" : ''}`} id="email" name="email" ref={register({ required: 'Email is required', pattern: { value: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/, message: 'Email is not valid' } })} />
            { errors.email && <div className="invalid-feedback">{errors.email.message}</div>}
            <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
            <div className="form-check mb-2">
                <input className="form-check-input" type="checkbox" name="mailingList" id="mailingList" ref={register} />
                <label className="form-check-label" htmlFor="mailingList">
                    Email me about new treats, delivery days and offers
                </label>
            </div>
            <button type="submit" className="btn btn-lg btn-success m-2" style={{ fontFamily: "Permanent Marker", fontSize: "2em" }}>Continue</button>
            { (errors.email || errors.name || errors.phone || errors.note) && <b style={{ color: "red" }}> Please fill out the required fields ‚¨ÜÔ∏è</b>}
        </form >
    );
}