import React, { Component } from 'react';

interface AddressProps {
    onValidAddressCallback: (address: string) => void
    onInvalidAddressCallback: () => void
    initialAddress?: string;
}

interface AddressState {
    autocomplete: google.maps.places.Autocomplete | null;
    autocompleteListener: google.maps.MapsEventListener | null;
    validAddress: AddressValidity;
    showArea: boolean;
}

enum AddressValidity {
    NotEntered = "NOT_ENTERED",
    Invalid = "INVALID",
    Valid = "VALID",
}

class Address extends Component<AddressProps, AddressState> {
    constructor(props: AddressProps) {
        super(props);

        this.state = {
            autocomplete: null,
            autocompleteListener: null,
            validAddress: AddressValidity.NotEntered,
            showArea: false,
        }
    }

    componentDidMount(): void {
        const autocomplete = new google.maps.places.Autocomplete(
            document.getElementById("autocompleteAddress") as HTMLInputElement,
            // https://developers.google.com/maps/documentation/javascript/places-autocomplete#summary-of-classes
            { types: ["address"] }
        );

        // Avoid paying for data that you don't need by restricting the set of
        // place fields that are returned to just the address components.
        // https://developers.google.com/maps/documentation/javascript/reference/places-service#PlaceResult.address_components
        // TODO: is the example wrong with "address_component", PR it
        autocomplete.setFields(["geometry", "formatted_address"]);

        // Bind the completion of an address event to check it's in the delivery area
        const listener = autocomplete.addListener("place_changed", this.isInDeliveryArea.bind(this));

        this.setState({ autocomplete: autocomplete, autocompleteListener: listener })
    }

    componentWillUnmount(): void {
        if (this.state.autocompleteListener) {
            google.maps.event.removeListener(this.state.autocompleteListener)
        }
    }

    isInDeliveryArea(): void {
        const deliveryCoords = [
            { lng: -2.0119413, lat: 51.4502812 },
            { lng: -2.0185072, lat: 51.4471873 },
            { lng: -2.0200522, lat: 51.4432821 },
            { lng: -2.0249445, lat: 51.4422389 },
            { lng: -2.0242579, lat: 51.4398582 },
            { lng: -2.0229275, lat: 51.4380926 },
            { lng: -2.0162327, lat: 51.4310028 },
            { lng: -2.0101387, lat: 51.4292637 },
            { lng: -2.0034762, lat: 51.4263587 },
            { lng: -2.004002, lat: 51.4249605 },
            { lng: -2.0029183, lat: 51.4229535 },
            { lng: -2.0001503, lat: 51.4224785 },
            { lng: -1.9974788, lat: 51.4221641 },
            { lng: -1.9947644, lat: 51.4223112 },
            { lng: -1.9893249, lat: 51.423676 },
            { lng: -1.9812997, lat: 51.4222711 },
            { lng: -1.9782742, lat: 51.4213478 },
            { lng: -1.9745191, lat: 51.4221774 },
            { lng: -1.9738754, lat: 51.4261245 },
            { lng: -1.9750985, lat: 51.4270209 },
            { lng: -1.9803127, lat: 51.4300579 },
            { lng: -1.9836601, lat: 51.4340979 },
            { lng: -1.9880804, lat: 51.4361312 },
            { lng: -1.9881233, lat: 51.4385924 },
            { lng: -1.9875654, lat: 51.4397159 },
            { lng: -1.985205, lat: 51.4419629 },
            { lng: -1.9861492, lat: 51.444718 },
            { lng: -1.9888958, lat: 51.4469381 },
            { lng: -1.9887241, lat: 51.4483289 },
            { lng: -1.9913848, lat: 51.4510568 },
            { lng: -1.9906982, lat: 51.4527149 },
            { lng: -1.9914278, lat: 51.455095 },
            { lng: -1.9938739, lat: 51.4552555 },
            { lng: -1.9996675, lat: 51.4543997 },
            { lng: -2.0045599, lat: 51.4535707 },
            { lng: -2.0052894, lat: 51.4530893 },
            { lng: -2.0119413, lat: 51.4502812 },
        ];

        if (this.state.autocomplete) {
            const placeResult = this.state.autocomplete.getPlace()
            if (placeResult.geometry && placeResult.geometry.location && placeResult.formatted_address) {
                const deliveryPoly = new google.maps.Polygon({
                    paths: deliveryCoords
                })

                const inDeliveryZone = google.maps.geometry.poly.containsLocation(placeResult.geometry.location, deliveryPoly)
                if (inDeliveryZone) {
                    this.setState({ validAddress: AddressValidity.Valid })
                    this.props.onValidAddressCallback(placeResult.formatted_address)
                } else {
                    this.setState({ validAddress: AddressValidity.Invalid })
                }
                return
            }
        }
        throw new Error("Unable to load place lat lang and check within delivery poly");
    }

    render(): JSX.Element {
        let areaWarning: JSX.Element | null
        switch (this.state.validAddress) {
            case AddressValidity.Invalid:
                areaWarning = <div className="alert alert-danger text-center" role="alert">
                    Sorry, your not in our delivery area yet, but we are expanding quickly. Sign up for our mailing list ‚¨áÔ∏è to get notified when our delivery zone expands.
                    <br />
                    <button className="btn btn-primary" onClick={() => { this.setState({ showArea: !this.state.showArea }) }}>Show delivery area map</button>
                    <br />
                    {this.state.showArea ? <img src="https://cookieassets.s3.eu-west-2.amazonaws.com/deliveryArea3.png" className="my-2" width="100%"></img> : null}
                </div>
                break;
            case AddressValidity.Valid:
                areaWarning = <div className="alert alert-success text-center" role="alert">
                    üéâ Great, you&apos;re in our delivery area üéâ
              </div>
                break;
            case AddressValidity.NotEntered:
                areaWarning = null;
                break;
        }

        return <>
            <input style={{ width: "100%" }} type="text" id="autocompleteAddress" placeholder="Start typing your address..." defaultValue={this.props.initialAddress || ''} />
            {areaWarning}
        </>
    }
}

export default Address;
