import { loadStripe, Stripe } from "@stripe/stripe-js";
import { apiLocation } from "./envvars";

enum ProductType {
    ChocChipCookie = 'CHOC_CHIP_COOKIE',
    AppleCake = 'APPLE_CAKE',
    AppleBerryCrumbleCookie = 'APPLE_BERRY_CRUMBLE_COOKIE',
    WhiteChocCranberryCookie = 'WHITE_CHOC_CRANBERRY_COOKIE'
}

// StripeCheckout is used to load in the stripe js SDK and call
// the backend with the order details. Must be initialized with
// init().
class StripeCheckout {
    private stripe: Stripe | null;
    private publicKey: string;

    constructor(publicKey: string) {
        this.publicKey = publicKey;
        this.stripe = null;
    }

    init = async (): Promise<void> => {
        const stripe = await loadStripe(this.publicKey)
        if (stripe === null) {
            throw new Error("Unable to load Stripe integration");
        }
        this.stripe = stripe;
    }

    checkout = async (order: {
        timeslot: Date,
        // TODO: add other products
        // products: Array<Products>,
        // boxes: number[],
        boxes: number,
        address: string,
        name: string,
        phone: string,
        note: string,
        email: string,
        mailingList: boolean
    }): Promise<void> => {

        const requestHeaders: HeadersInit = new Headers();
        requestHeaders.set('Content-Type', 'application/json');
        const response = await fetch(apiLocation() + "/create-session", {
            method: "POST",
            body: JSON.stringify({
                timeslot: order.timeslot.toISOString(),
                products: [ProductType.ChocChipCookie],
                boxes: [order.boxes],
                address: order.address,
                name: order.name,
                phone: order.phone,
                note: order.note,
                email: order.email,
                mailingList: order.mailingList,
            }),
            headers: requestHeaders,
        });

        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        const session = await response.json();

        if (this.stripe === null) {
            throw new Error("Stripe didn't init");
        }

        // When the customer clicks on the button, redirect them to Checkout.
        const result = await this.stripe.redirectToCheckout({
            // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access, @typescript-eslint/no-unsafe-assignment
            sessionId: session.id,
        });

        if (result.error) {
            // If `redirectToCheckout` fails due to a browser or network
            // error, use `result.error.message`.
            throw new Error(result.error.message);
        }
    };
}

export default StripeCheckout;
