import { Loader } from '@googlemaps/js-api-loader';
import React, { useEffect, useState } from 'react';
import Address from './Address';
import { FreeTimeSlot, getFreeTimeslots } from './API';
import CustomerForm, { CustomerDetails } from './CustomerForm';
import DateCookieForm, { OrderInfo } from './DateCookieForm';
import { orderEmail, orderPhoneNo, stripeKey } from './envvars';
import StripeCheckout from './StripeCheckout';

enum FormState {
    Loading = "LOADING",
    TimeSlotForm = "TIMESLOT_FORM",
    AddressForm = "ADDRESS_FORM",
    CustomerForm = "CUSTOMER_FORM",
    NoFreeTimeslots = "NO_FREE_TIMESLOTS",
    GetTimeSlotError = "GET_TIME_SLOT_ERROR",
    Checkout = "CHECKOUT",
}

export default function DeliveryForm(): JSX.Element {
    const [formState, setFormState] = useState<FormState>(FormState.Loading);
    const [googleInitialised, setGoogleInitialised] = useState<boolean>(false);
    const [freeTimeSlots, setFreeTimeSlots] = useState<FreeTimeSlot[]>([]);

    const [orderInfo, setOrderInfo] = useState<OrderInfo | undefined>(undefined);
    const [address, setAddress] = useState<string | undefined>(undefined);
    const [customerDetails, setCustomerDetails] = useState<CustomerDetails | undefined>(undefined);

    useEffect(() => {
        getFreeTimeslots()
            .then((freeTimeSlots) => {
                if (freeTimeSlots.length == 0) {
                    setFormState(FormState.NoFreeTimeslots);
                    return
                }
                setFreeTimeSlots(freeTimeSlots);
                setFormState(FormState.TimeSlotForm);
            })
            .catch((error) => {
                console.error("Unable to get free timeslots from cookie API", error);
                setFormState(FormState.GetTimeSlotError);
            })
    }, [])

    switch (formState) {
        case FormState.Loading:
            return <LoadingSpinner />

        case FormState.TimeSlotForm:
            return <>
                <DateCookieForm
                    freeTimeSlots={freeTimeSlots}
                    callback={(order) => { setOrderInfo(order) }}
                    initialOrderInfo={orderInfo}
                />
                <br />
                <br />
                <button className="btn btn-lg btn-success m-2" style={{ fontFamily: "Permanent Marker", fontSize: "2em" }} onClick={async () => {
                    if (!googleInitialised) {
                        await initGoogleMaps()
                        setGoogleInitialised(true);
                    }
                    setFormState(FormState.AddressForm)
                }}>Continue</button>
            </>

        case FormState.AddressForm:
            window.location.hash = "order_form";

            return <>
                <label htmlFor="timeSlot" className="form-label">
                    <h3 className="mt-2" style={{ fontFamily: "Permanent Marker" }}>Where:</h3>
                </label>
                <br />
                <label htmlFor="Address" className="form-label mt-2">Address:</label>
                <Address
                    onValidAddressCallback={(address) => {
                        setAddress(address);
                    }}
                    onInvalidAddressCallback={() => { console.log("Unable to deliver to this address") }}
                    initialAddress={address}
                />
                <br />
                { address ? null : <b className="mx-2" style={{ color: "red" }}> Add an address to continue</b>}
                <br />
                <button
                    className="btn btn-lg btn-success m-2"
                    style={{ fontFamily: "Permanent Marker", fontSize: "2em" }}
                    disabled={address === undefined}
                    onClick={() => {
                        if (address) {
                            setFormState(FormState.CustomerForm)
                        }
                    }}>Continue</button>
                <br />
                <button className="btn btn-lg btn-secondary m-2" style={{ fontFamily: "Permanent Marker" }} onClick={() => setFormState(FormState.TimeSlotForm)}>Back</button>
            </>;

        case FormState.CustomerForm:
            window.location.hash = "order_form";

            return <>
                <CustomerForm
                    callback={(customerDetails) => {
                        setCustomerDetails(customerDetails);
                        setFormState(FormState.Checkout);
                    }}
                    initialCustomerDetails={customerDetails}
                />
                <button className="btn btn-lg btn-secondary m-2" style={{ fontFamily: "Permanent Marker" }} onClick={() => setFormState(FormState.AddressForm)}>Back</button>
            </>

        case FormState.NoFreeTimeslots:
            return <div className="text-center">

                <h3 style={{ fontFamily: "Permanent Marker" }}>Sorry, we&apos;re fully booked week!</h3>
                <p>
                    Check back next week for more delivery slots<br />
                    ‚¨áÔ∏è In the mean time why not sign up for our mailing list for delicious new treat updates ‚¨áÔ∏è
                </p>
            </div>

        case FormState.GetTimeSlotError:
            return <div className="row">
                <div className="col-12 text-center">
                    <h3>Sorry, the order form isn't working right now, we&apos;re having some technical issues!</h3>
                    In the mean time contact us by phone on <a href={orderPhoneNo}>{orderPhoneNo}</a> or email <a href={orderEmail}>{orderEmail}</a> to place an order.
                </div>
            </div>

        case FormState.Checkout:
            window.location.hash = "order_form";

            if (!orderInfo || !address || !customerDetails) {
                throw new Error("Missing order details");
            }

            return <>
                <Checkout orderInfo={orderInfo} address={address} customerDetails={customerDetails} />
                <br />
                <button className="btn btn-lg btn-success m-2" style={{ fontFamily: "Permanent Marker", fontSize: "2em" }} onClick={async () => {
                    const initialisedStripeCheckout = await initStripeCheckout();

                    await initialisedStripeCheckout.checkout({
                        timeslot: orderInfo.date,
                        boxes: orderInfo.boxes,
                        address: address,
                        name: customerDetails.name,
                        phone: customerDetails.phone,
                        note: customerDetails.note,
                        email: customerDetails.email,
                        mailingList: customerDetails.mailingList
                    })
                }}>Checkout</button>
                <br />
                <button className="btn btn-lg btn-secondary m-2" style={{ fontFamily: "Permanent Marker" }} onClick={() => setFormState(FormState.CustomerForm)}>Back</button>
            </>
    }
}


async function initGoogleMaps(): Promise<void> {
    const loader = new Loader({ apiKey: "AIzaSyAyUpor6TH4ZMVMAjcxfmgTfNRye-mmxco", version: "weekly", libraries: ["places", "geometry"] });
    await loader.load();
    return;
}

async function initStripeCheckout(): Promise<StripeCheckout> {
    const stripeCheckout = new StripeCheckout(stripeKey());
    await stripeCheckout.init();
    return stripeCheckout;
}

// TODO: into own file
const LoadingSpinner = (): JSX.Element => {
    return <div className="spinner-border" role="status">
        <span className="visually-hidden">Loading...</span>
    </div>
}

function Checkout(props: { orderInfo: OrderInfo, address: string, customerDetails: CustomerDetails }): JSX.Element {
    return <>
        <h3 className="mt-2" style={{ fontFamily: "Permanent Marker" }}>Your Order:</h3>
        {`${props.orderInfo.boxes} ${props.orderInfo.boxes === 1 ? "box" : "boxes"} of Giant Milk Chocolate Chip Cookies`}
        <br />
        {`${props.orderInfo.boxes} x ¬£10 per box`}
        <br />
        {`Total: ¬£${props.orderInfo.boxes * 10}`}
        <hr />

        {`Delivery date: ${props.orderInfo.date.toDateString()}`}
        <br />
        {`Delivery time: ${props.orderInfo.date.toLocaleTimeString()}`}
        <br />
        {`Name: ${props.customerDetails.name}`}
        <br />
        {`Address: ${props.address}`}
        <br />
        {`Phone: ${props.customerDetails.phone}`}
        <br />
        {`Email: ${props.customerDetails.email}`}
        <br />
        {props.customerDetails.note !== "" ? `Delivery note: "${props.customerDetails.note}"` : null}
        <br />
    </>
}