import React from 'react';
import { FreeTimeSlot } from './API';

interface AnnotatedFreeTimeSlots extends FreeTimeSlot {
    day: string;
    time: string;
}

function dateToDay(date: Date): string {
    return date.toDateString();
}

function dateToTimeWindow(date: Date): string {
    const startTime = date.toTimeString().substring(0, 5);
    const endTime = new Date(date.getTime() + (30 * 60000)).toTimeString().substring(0, 5);
    return `${startTime} - ${endTime}`;
}

function annotateFreeTimeSlots(freeTimeSlots: FreeTimeSlot[]): AnnotatedFreeTimeSlots[] {
    return freeTimeSlots
        .map(
            function (freeTimeSlot) {
                const elementDay = dateToDay(freeTimeSlot.date);
                const time = dateToTimeWindow(freeTimeSlot.date);
                return {
                    ...freeTimeSlot,
                    day: elementDay,
                    time: time,
                }
            }
        )
}

export interface OrderInfo {
    date: Date;
    boxes: number;
}

export default function DateCookieForm(props: { freeTimeSlots: FreeTimeSlot[], callback: (order: OrderInfo) => void, initialOrderInfo?: OrderInfo }): JSX.Element {
    const annotatedFreeTimeSlots = annotateFreeTimeSlots(props.freeTimeSlots);

    const day = props.initialOrderInfo ? dateToDay(props.initialOrderInfo.date) : annotatedFreeTimeSlots[0].day;
    const time = props.initialOrderInfo ? dateToTimeWindow(props.initialOrderInfo.date) : annotatedFreeTimeSlots[0].time;
    const boxes = props.initialOrderInfo ? props.initialOrderInfo.boxes : 1;

    const days: string[] = annotatedFreeTimeSlots.map((fts) => fts.day).filter((elem, index, self) => index === self.indexOf(elem))
    const times: string[] = annotatedFreeTimeSlots.filter((elem) => elem.day === day).map((fts) => fts.time);
    const availableBoxes: number = annotatedFreeTimeSlots.filter((elem) => elem.day === day && elem.time === time).reduce((acc, fts) => acc + fts.boxes, 0);

    // TODO: is this a hack, should the caller be responsible for always using an existingOrderInfo?
    if (!props.initialOrderInfo) {
        const dates = annotatedFreeTimeSlots.filter((elem) => elem.day === day && elem.time === time);
        if (dates.length !== 1) {
            throw new Error("multiple dates returned for a selected day and time");
        }
        props.callback({
            boxes: boxes,
            date: dates[0].date
        })
    }

    return <>
        <h3 className="mt-2" style={{ fontFamily: "Permanent Marker" }}>When:</h3>
        <DaySelect
            availableDays={days}
            selectedDay={day}
            selectedDayCallback={(newDay) => {
                // new day, reset time, reset boxes
                const dates = annotatedFreeTimeSlots.filter((elem) => elem.day === newDay);
                props.callback({
                    date: dates[0].date,
                    boxes: 1,
                })
            }}
        />

        <TimeSelect
            availableTimes={times}
            selectedTime={time}
            selectedTimeCallback={(newTime) => {
                // old day, new time, reset boxes
                const dates = annotatedFreeTimeSlots.filter((elem) => elem.day === day && elem.time === newTime);
                props.callback({
                    date: dates[0].date,
                    boxes: 1,
                })
            }}
        />

        <h3 className="mt-3" style={{ fontFamily: "Permanent Marker" }}>How many Cookies:</h3>
        <h3>Giant Milk Chocolate Chip</h3>
        <p>
            Allergens: <b>Egg, Milk, Soy, Gluten</b>
            <div id="emailHelp" className="form-text">¬£10 per box with free delivery!</div>
        </p>
        <CookieSelect
            availableBoxes={availableBoxes}
            selectedBoxes={boxes}
            selectedBoxesCallback={(newBoxes: number) => {
                // old day, old time, new boxes
                const dates = annotatedFreeTimeSlots.filter((elem) => elem.day === day && elem.time === time);
                props.callback({
                    date: dates[0].date,
                    boxes: newBoxes,
                })
            }} />
    </>;
}

function TimeSelect(props: { availableTimes: string[], selectedTime: string, selectedTimeCallback: (day: string) => void }): JSX.Element {
    const timeOptions: JSX.Element[] = [];
    for (const availableDay of props.availableTimes)
        timeOptions.push(<option key={availableDay} value={availableDay}>{availableDay}</option>)

    return <>
        <label htmlFor="timeSelect" className="form-label mt-2">Time:</label>
        <select id="timeSelect" className="form-select" onChange={(e) => props.selectedTimeCallback(e.target.value)} value={props.selectedTime}>
            {timeOptions}
        </select>
    </>;
}

function DaySelect(props: { availableDays: string[], selectedDay: string, selectedDayCallback: (day: string) => void }): JSX.Element {
    const dayOptions: JSX.Element[] = [];
    for (const availableDay of props.availableDays)
        dayOptions.push(<option key={availableDay} value={availableDay}>{availableDay}</option>)

    return <>
        <label htmlFor="daySelect" className="form-label mt-2">Day:</label>
        <select id="daySelect" className="form-select" onChange={(e) => props.selectedDayCallback(e.target.value)} value={props.selectedDay}>
            {dayOptions}
        </select>
    </>;
}

function CookieSelect(props: { availableBoxes: number, selectedBoxes: number, selectedBoxesCallback: (boxes: number) => void }): JSX.Element {
    const buttons: JSX.Element[] = [];
    for (let i = 1; i <= props.availableBoxes; i++) {
        buttons.push(
            <button
                key={i}
                className={"btn btn-lg m-2 btn-" + (props.selectedBoxes === i ? "primary" : "secondary")}
                onClick={() => props.selectedBoxesCallback(i)}
            >
                {i} {i === 1 ? "box" : "boxes"}
                <br />
                ({i * 4} cookies)
            </button>
        )
    }

    return <>
        {buttons}
    </>;
}
